h1 {
  font-size: 2.8rem;
}

h2 {
  font-size: 2rem;
}

.main {
  min-height: calc(100vh - 8rem);
  background-color: #f6f6f6;
}

.container {
  max-width: 120rem;
  margin: 0 auto;
  padding: 2rem;
}

.board-header {
  text-align: center;
  padding-bottom: 4rem;
}

.board-items {
  flex-wrap: wrap;
